<template>
    <div>

        <nav-bar :name="title"></nav-bar>

            <v-card class="ma-1" cols="12">
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-select
                        class="mb-n5"
                        outlined
                        dense
                        :items="items"
                        v-model="items_payment.payment"
                        label="*Payment"
                        ></v-select>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        class="mb-n5"
                        outlined
                        dense
                        label="*Issue description"
                        v-model="items_payment.issue_description"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                    <v-btn
                    class="mx-4 my-2"
                    color="#989898"
                    dark
            @click="$router.go(-1)"
                    >
                    GO BACK
                    </v-btn>
                    <v-btn
                    class="mx-4 my-2"
                    color="#E47911"
                    dark
                    :loading="loading"
                    @click="send_report_problem"
                    >
                    SEND REPORT
                    </v-btn>
                    </v-col>
                </v-row>
            </v-container>
            </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import PaymentService from '@/services/PaymentService';
import Swal from 'sweetalert2'

export default {
    components: {
        NavBar
    },

    data: () => ({
        title: 'REPORT PROBLEM',
        loading: false,
        items: [],
        payment: '',
        items_payment: {
            payment: '',
            issue_description: '',
        },
    }),

    computed:{
        idStore(){
            return this.$store.state.auth.store_user.unique_id;
        }
    },

    mounted() {
        PaymentService
        .load_payment(this.idStore)
        .then(res => {
            res.items.forEach(item => {
                this.items.push({ text: 'Payment: ' + item.unique_id , value: item.unique_id})
            });
        })
        .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
    },

    methods:{
        send_report_problem(){
            this.loading = true;
            PaymentService
            .send_report_problem(this.items_payment)
            .then(res => {
                this.loading = false;
                Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: 'The report payment has sent.'
                }).then(() => {
                    return this.$router.go(-1)
                })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        }
    }
    
}
</script>